
#app {
  margin-top: 60px;
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}

footer {
  margin-top: 80px;
  background: white!important;
}

h1  {
  font-family: 'Carter One', cursive;
  font-size: 80px;
  color: #00d1b2;
  -webkit-text-stroke: 1px black;
}

.play-round {
  margin-top: 60px;
  font-family: 'Montserrat', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  font-size: 30px;
}
.questionArea {
  margin-top: 40px;
}
input[type="text"]{
  text-align: center;
}
.timerArea {
  margin-top: 40px;
}
.answerArea {
  margin-top: 40px;
}
.resultArea {
  padding-top: 40px;
}
#nextRoundBtn {
  margin-top: 20px;
}
